@media screen and (max-width:600px)
{
    .about__cards{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap:1rem;
    }
    .about-container{
      
        grid-template-columns: 1fr;
        gap:1rem;
        text-align: center;
    
    }   
    .about-academic{
        text-align: center;
        margin-bottom: 1rem;
    }
    .about__me{
        width: 65%;
        margin:0 auto 3rem;
    }
    .about-content p{
        margin:1.5rem 0;
        text-align: justify;
    }
}
@media screen and (max-width:450px) {
    .about__cards{
       
        grid-template-columns: repeat(1,1fr);
       text-align: center;
    }
}